body {
  color: #fff;
  background-color: #232323;
  font-family: "Maven Pro", sans-serif;
}

button, .button-primary {
  border-radius: 4px;
  letter-spacing: 0.05rem;
}
button:hover, .button-primary:hover {
  cursor: pointer;
}

.button-dark {
  color: #fff;
  background-color: #232323;
  border-radius: 0;
  height: 2rem;
}
.button-dark:hover {
  background-color: #f65261;
}
.button-dark:focus {
  outline: none;
  border: none;
}

h1 {
  font-size: 32px;
  font-weight: normal;
}

.button-primary {
  width: 12rem;
  height: 3rem;
  background-color: #f65261;
  color: #fff;
  font-size: 1.3rem;
}
.button-primary:hover {
  background-color: #f76371;
}

.button-secondary {
  width: 12rem;
  height: 3rem;
  border: 1px solid #f65261;
  color: #f65261;
  background-color: #232323;
  font-size: 1.3rem;
}
.button-secondary:hover {
  background-color: #393939;
}

.custom_input, .form-input {
  height: 3rem;
  border-radius: 5px;
  border: none;
  padding: 1rem;
  background-color: rgba(66, 66, 66, 0.5);
  font-size: 1.2rem;
  color: #fff;
  text-overflow: ellipsis;
}

.app-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
}
.app-container .app-container__inner {
  display: flex;
  flex-direction: column;
  flex: 1 0 auto;
}

.button-invalid {
  background-color: #555555;
}
.button-invalid:hover {
  background-color: #555555;
  cursor: default;
}

.validation-error-diplay {
  color: #f87581;
  margin: 0.3rem 0 0.5rem;
  font-size: 1rem;
}

.form-label {
  color: #f65261;
}

.form-input {
  margin: 0.3rem 0 1rem;
}
.form-input.disabled {
  margin: 0;
  background-color: #232323;
}
.form-input.invalid-field {
  border: 1px solid #f87581;
  margin-bottom: 0;
}